<template>
  <div class="file-manager">
    <!-- 上传区域 -->
    <el-upload
      action="#"
      multiple
      :http-request="handleUpload"
      :show-file-list="false"
    >
      <el-button type="primary">上传文件</el-button>
    </el-upload>

    <!-- 文件列表 -->
    <el-table :data="files">
      <el-table-column prop="name" label="文件名"></el-table-column>
      <el-table-column label="操作">
        <template v-slot="{ row }">
          <el-button 
            v-if="row.type === 'file'"
            @click="downloadFile(row.path)"
          >下载</el-button>
          <el-button 
            type="danger"
            @click="deleteFile(row.path)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import request from '../api/request';

export default {
  name: 'FileManager',
  data() {
    return {
      files: []
    }
  },
  async mounted() {
    await this.loadFiles();
  },
  methods: {
    async loadFiles() {
      const res = await request.get('/files');
      this.files = res.data;
    },
    
    async handleUpload({ file }) {
      const formData = new FormData();
      formData.append('files', file);
      
      try {
        await request.post('/upload', formData, {
          headers: { 'Content-Type': 'multipart/form-data' }
        });
        await this.loadFiles();
      } catch (err) {
        this.$message.error('上传失败');
      }
    },
    
    async deleteFile(path) {
      try {
        await request.delete(`/files/${encodeURIComponent(path)}`);
        await this.loadFiles();
      } catch (err) {
        this.$message.error('删除失败');
      }
    },
    
    downloadFile(path) {
      window.open(
        `http://localhost:3000/api/download/${encodeURIComponent(path)}`,
        '_blank'
      );
    }
  }
}
</script>
