import VueRouter from 'vue-router'
import Vue from 'vue'
Vue.use(VueRouter);
import MyLogin from '../components/MyLogin.vue'
import FileManager from '../components/FileManager.vue'
export default new VueRouter({
    routes: [
        {
            path: '/login',
            component: MyLogin
        },
        {
            path: '/file',
            component: FileManager
        },
    ]
})